.app-container {
  display: flex;
  
  align-items: center;
  min-height: 100vh; 
}

.loading-container {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.7);

}


@media (min-width: 320px) and (max-width: 480px) {
  .App {
    background-color: black;
  }

  
}
